import React, { useEffect, useState } from "react";
import {
  Table,
  Pagination,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Button,
} from "react-bootstrap";
import { FaRegEye, FaRegComments } from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { IconContext } from "react-icons";
import { useDispatch } from "react-redux";
import { setOrder } from "student/store/order";
import { getAllOrderApi, getUserTranslationOrderApi } from "student/http";
import { useSelector } from "react-redux";
import moment from "moment";

import styles from "./Table.module.css";

const TableBoot = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const {user} = useSelector(s=>s.auth);

  const [tableData, setTableData] = useState([]);
  const [totalData, setTotalData] = useState(5);
  const [isUnmount, setIsUnmount] = useState(false);

  // ;
  const [pagData, setPagData] = useState({
    page: 0,
    row: 5,
  });

  React.useEffect(() => {
       
      if(user._id){
          const tempApi =
            (user.role === "admin")
              ? getAllOrderApi(pagData)
              : getUserTranslationOrderApi(user._id, pagData);
         ( async()=>{
               try {
                 const { data } = await tempApi;
                   
                 if (data) {
                   console.log(data);
             
                     if (data.data) {
                       setTableData(data.data);
                       setTotalData(data.total);
                     }

                 }
               } catch (e) {
                 console.log(e);
               }
          })();
      }
  

    return () => {
      setTableData([]);
      setIsUnmount(true);
    };
  }, [user,pagData]);

  const pagNumber =
    [...Array(Math.ceil(+totalData / +pagData.row) ?? 10).keys()] || [];

  const pagHandler = (a) => {
    if (a === "next") {
      if (pagData.page !== pagNumber.length - 1)
        setPagData({ ...pagData, page: +pagData.page + 1 });
    } else {
      if (pagData.page >= 1)
        setPagData({ ...pagData, page: +pagData.page - 1 });
    }
  };

  console.log(pagData)


  const viewClickHandler = (v) => {
    dispatch(setOrder(v));
    const url =
      user.role === "admin"
        ? "/admin/dashboard/inner-order"
        : "/dashbord/inner-order";
    history.push(url);
  };
  const messageClickHandler = (v) => {
    dispatch(setOrder(v));
   const url = user.role === "admin"
      ? "/admin/dashboard/chat-support"
      : "/dashbord/chat-support";
    history.push(url);
  };

  return (
    <div className="bg-white rounded overflow-scroll">
      <IconContext.Provider
        value={{ color: "#000", size: 20, paddingLeft: 10 }}
      >
        <Table striped className={styles.table}>
          <thead className={styles.tableInfo}>
            <tr className={styles.tableInfo}>
              <th>Order Id</th>
              <th>Service Required</th>
              <th>Source Language</th>
              <th>Target Language</th>
              <th>Deadline</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody
            className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
          >
            {tableData.length ? (
              tableData.map((e) => {
                return (
                  <tr key={e._id} className={styles.tableInfo}>
                    <td>{e._id}</td>
                    <td>{e.service_req}</td>
                    <td>{e.sourceLanguage}</td>
                    <td>{e.targetlanguage}</td>
                    <td>{moment(e.deadline).format("DD/MM/YYYY")}</td>
                    <td>
                      <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                      >
                        <Button
                          variant="link"
                          onClick={() => viewClickHandler(e)}
                        >
                          <FaRegEye />
                        </Button>
                      </OverlayTrigger>
                      <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id={`tooltip-top`}>messages</Tooltip>}
                      >
                        <Button
                          variant="link"
                          onClick={() => messageClickHandler(e)}
                        >
                          <FaRegComments />
                        </Button>
                      </OverlayTrigger>

                      <Link to="#"></Link>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr className={styles.tableInfo}>
                <td colSpan="5" className="text-center bg-white">
                  There is no Any Order{" "}
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </IconContext.Provider>
      <Row className="p-1 m-2">
        <Col md={6} className="text-style">
          <p>
            Showing {+pagData.page * +pagData.row} to{" "}
            {(+pagData.page + 1) * pagData.row} of {totalData} entries
          </p>
        </Col>
        <Col md={6}>
          <Pagination>
            <Pagination.Item onClick={() => pagHandler("pre")}>
              {"Privious"}
            </Pagination.Item>
            {pagNumber.map((e) => {
              return (
                <Pagination.Item
                  onClick={() => setPagData({ ...pagData, page: +e })}
                  active={pagData.page === +e}
                  key={e}
                >
                  {+e + 1}
                </Pagination.Item>
              );
            })}
            <Pagination.Item onClick={() => pagHandler("next")}>
              {"Next"}
            </Pagination.Item>
          </Pagination>
        </Col>
      </Row>
    </div>
  );
};

export default TableBoot;
