import { createSlice } from "@reduxjs/toolkit";
import { setSeenOrderApi } from "student/http";

const unsMessagesSlice = createSlice({
  name: "unsm",
  initialState: {
    messages: [],
    orderUnSm: [],
  },
  reducers: {
    setUnSeenMessages: (state, action) => {
      const load = action.payload;
      // const ousm = load.filter(e=>e.orderId);
      const tempLoad = [];
      load.forEach((e) => {
        const index = tempLoad.findIndex((f) => f.orderId === e.orderId);
        index !== -1
          ? (tempLoad[index] = {
              ...tempLoad[index],
              count: +tempLoad[index].count + 1,
            })
          : tempLoad.push({ ...e, count: 1 });
      });
      state.messages = tempLoad;
      // state.orderUnSm = ousm;
    },
    addUnSeenMessage: (state, action) => {
      const temp = action.payload;
      let load = state.messages;
      const index = load.findIndex((e) => e.orderId === temp.orderId);
      if (index === -1) {
        load.unshift({ ...temp, count: 1 });
      } else {
        const td = +load[index].count;
        load = load.filter((e, i) => i !== index);
        load.unshift({ ...temp, _id: `${Math.random()}`, count: +td + 1 });
      }
      state.messages = load;
    },
    setSeenMessage: (state, action) => {
      const id = action.payload;
      const load = state.messages;
      state.messages = load.filter((e) => e.orderId !== id);
      try {
        const {data} = setSeenOrderApi(id);
        console.log(data);
      } catch (E) {
        console.log(E);
      }
    },
  },
});

export const { setUnSeenMessages, addUnSeenMessage, setSeenMessage } =
  unsMessagesSlice.actions;
export default unsMessagesSlice.reducer;
