import React from 'react'
import styles from './Search.module.css'
import { Col, Row, Button, Collapse, Card, Form, Tab, Nav, Container } from 'react-bootstrap';
import { FaSearch, FaSyncAlt, FaPaste, FaTachometerAlt, } from 'react-icons/fa';


const Search = ({text='Dashboard'}) => {
    const [open, setOpen] = React.useState(false);

    return (
        <div>
            <Row>
                <Col md={6}>
                    <h4 className={styles.dashboard}>{text}</h4>
                </Col>
                <Col md={6}>
                    <div className={styles.btnRight}>
                        <Button
                            className={styles.dashBoardBtn}
                            onClick={() => setOpen(!open)}
                            aria-controls="example-collapse-text"
                            aria-expanded={open}
                        >
                            <FaSearch />
                        </Button>
                    </div>
                </Col>
            </Row>
            <Collapse in={open} className='mt-2'>
                <div id="example-collapse-text">
                    <Card>
                        <Card.Body>
                            <Row>
                                <Col md={5} >
                                    <Row>
                                        <Col md={6} className='p-1'>
                                            <Form.Control placeholder="Enter Order Id" />
                                        </Col>
                                        <Col md={6} className='p-1'>
                                            <Form.Control placeholder="Enter Date" type='date' />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col md={5}>
                                    <Row>
                                        <Col md={6} className='p-1'>
                                            <Form.Control placeholder="Enter Contry" />
                                        </Col>
                                        <Col md={6} className='p-1'>
                                            <Form.Control placeholder="Enter Subject" type='text' />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col md={2} className='m-0 px-2 py-1 row'>
                                    <Row>
                                        <Col xs={6} md={6} className='m-0'>
                                            <Button className={styles.searchBtn}>
                                                <FaSearch />
                                            </Button>
                                        </Col>
                                        <Col xs={6} md={6}>
                                            <Button className={styles.refreshBtn}>
                                                <FaSyncAlt />
                                            </Button>
                                        </Col>
                                    </Row>
                                </Col>

                            </Row>
                        </Card.Body>
                    </Card>
                </div>
            </Collapse>

        </div>
    )
}

export default Search
