import React, { useEffect, useState } from "react";
import {
  Table,
  Pagination,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
  Button,
} from "react-bootstrap";
import { FaRegEye, FaRegComments } from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { IconContext } from "react-icons";
import { useDispatch } from "react-redux";
import { setOrder } from "student/store/order";
import moment from "moment";
import { getTranslationUesrsApi } from 'student/http'

import styles from "./UserTable.module.css";

const UserTable = ({role='user'}) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [tableData, setTableData] = useState([]);
  const [totalData, setTotalData] = useState(5);
  const [isUnmount, setIsUnmount] = useState(false);

  const [pagData, setPagData] = useState({
    page: 0,
    row: 5,
  });

  React.useEffect(async () => {
    try {
      const { data } = await getTranslationUesrsApi(pagData,role);
      // const { data } = await getUserOrderApi(user._id, pageData);
      //  const { data } = await getUserTranslationOrderApi(, pagData);
      if (data) {
        console.log(data)
        if (!isUnmount) {
          if (data.data) {
            setTableData(data.data);
            setTotalData(data.total);
            // setTotalOrders(data);
          }
        }
      }
    } catch (e) {
      console.log(e);
    }

    return () => {
      setTableData([]);
      setIsUnmount(true);
    };
  }, []);

  const pagNumber =
    [...Array(Math.ceil(+totalData / +pagData.row) ?? 10).keys()] || [];
  const pagHandler = (a) => {
    if (a === "next") {
      if (pagData.page !== pagNumber.length - 1)
        setPagData({ ...pagData, page: +pagData.page + 1 });
    } else {
      if (pagData.page >= 1)
        setPagData({ ...pagData, page: +pagData.page - 1 });
    }
  };

  const viewClickHandler = (v) => {
    dispatch(setOrder(v));
    history.push("/dashbord/inner-order");
  };
  const messageClickHandler = (v) => {
    dispatch(setOrder(v));
    history.push("/dashbord/chat-support");
  };

  return (
    <div className="bg-white rounded overflow-scroll">
      <IconContext.Provider
        value={{ color: "#000", size: 20, paddingLeft: 10 }}
      >
        <Table striped className={styles.table}>
          <thead className={styles.tableInfo}>
            <tr className={styles.tableInfo}>
              <th>Order Id</th>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Country</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody
            className={`${styles.tableInfo} ${styles.table} ${styles.tableBody} `}
          >
            {tableData.length ? (
              tableData.map((e) => {
                return (
                  <tr key={e._id} className={styles.tableInfo}>
                    <td>{e._id}</td>
                    <td>{e.name}</td>
                    <td>{e.email}</td>
                    <td>{e.phone}</td>
                    <td>{e.country}</td>
                    <td>
                      <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id={`tooltip-top`}>view</Tooltip>}
                      >
                        <Button
                          variant="link"
                          onClick={() => viewClickHandler(e)}
                        >
                          <FaRegEye />
                        </Button>
                      </OverlayTrigger>
                      <OverlayTrigger
                        placement="top"
                        overlay={<Tooltip id={`tooltip-top`}>messages</Tooltip>}
                      >
                        <Button
                          variant="link"
                          onClick={() => messageClickHandler(e)}
                        >
                          <FaRegComments />
                        </Button>
                      </OverlayTrigger>

                      <Link to="#"></Link>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr className={styles.tableInfo}>
                <td colSpan="5" className="text-center bg-white">
                  There is no Any Order{" "}
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </IconContext.Provider>
      <Row className="p-1 m-2">
        <Col md={6} className="text-style">
          <p>
            Showing {+pagData.page * +pagData.row} to{" "}
            {(+pagData.page + 1) * pagData.row} of {totalData} entries
          </p>
        </Col>
        <Col md={6}>
          <Pagination>
            <Pagination.Item onClick={() => pagHandler("pre")}>
              {"Privious"}
            </Pagination.Item>
            {pagNumber.map((e) => {
              return (
                <Pagination.Item
                  onClick={() => setPagData({ ...pagData, page: +e })}
                  active={pagData.page === +e}
                  key={e}
                >
                  {+e + 1}
                </Pagination.Item>
              );
            })}
            <Pagination.Item onClick={() => pagHandler("next")}>
              {"Next"}
            </Pagination.Item>
          </Pagination>
        </Col>
      </Row>
    </div>
  );
};

export default UserTable;
