import React from "react";
import { useHistory } from "react-router-dom";
import { Badge, Button, ListGroup, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { setSeenMessage } from "student/store/unsm";

const ListGrout = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { messages } = useSelector((s) => s.unsm);
  const listOnClickHandler = (e) => {
    dispatch(setSeenMessage(e.orderId));
    history.push("/dashbord/chat-support");
  };

  console.log("sjd", messages);
  return (
    <ListGroup as="ol" className="rounded" numbered>
      {messages.map((e) => (
        <ListGroup.Item
          key={e._id}
          action
          onClick={() => listOnClickHandler(e)}
          className="d-flex flex-column "
        >
          <div className=" w-100 d-flex flex-row justify-content-between">
            <div className="me-5">
              <div className="fw-bold">{e.sender?.name}</div>
              {e.message}
            </div>
            <div>
              <Badge variant="danger" pill>
                {e.count}
              </Badge>
            </div>
          </div>
          <div>
            <p>
              Order Id:
              <br />
              {e.orderId}
            </p>
          </div>
        </ListGroup.Item>
      ))}
    </ListGroup>
  );
};

export default ListGrout;
