import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import * as Yup from 'yup';
import Input from '@mui/material/Input';
import OutlinedInput from '@mui/material/OutlinedInput';

import InputLabel from "@material-ui/core/InputLabel";
import Box from '@mui/material/Box';
import { useFileUpload } from 'use-file-upload'
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "client/components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import NormalSelect from 'components/NormalSelect'
import DatePicker from 'client/components/DatePicker'
import Avatar from '@mui/material/Avatar';
import CountrySelect from "client/components/CountrySelect.js";
import TextField from '@mui/material/TextField';


import avatar from "assets/img/faces/marc.jpg";
import useForm from "components/validation/useForm";
import Validator from "components/validation/Validator";
import { HOST } from '../../config.js';
import { ToastContext } from "client/index";
import { LoginContext } from "client/index.js";


const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function UserProfile() {
  const { setToastData } = React.useContext(ToastContext);
  const { userData } = React.useContext(LoginContext);

  const classes = useStyles();

  const sendRequest = (value) => {
    
    console.log('Before Api',value);
    for (var valu of value.values()) {
      console.log(valu);
   }
    fetch(`${HOST}/api/mainorder`, {
      method: 'POST',
      body: value
    }).then(res => (res.json()))
      .then(res => {
        handleReset();
        if (!res.ok) {
          setToastData({ state: true, message: res.message, type: 'success' })
        } else {
          setToastData({ state: true, message: res.message, type: 'error' })
        }
        console.log(res)
      })
  }

  React.useEffect(() => {
    setValues({
      ...values,
      user: userData._id,
      name: userData.name,
      email: userData.email
    })
  }, [userData])


  const validationSchema = Yup.object().shape({
    subject: Yup.string().required(),
    user: Yup.string().required(),
    name: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
    email: Yup.string().email('Invalid email').required('Email is Required'),
    question: Yup.string().required('Question is Required'),
    education: Yup.string().required('Education is Required'),
    file: Yup.string().notRequired()
  });

  const initValue = {
    subject: '',
    name: '',
    email: '',
    education: '',
    question: '',
    files: '',
    deadline: null,
    contact: '',
    country: '',
    paperType:'',
    paperLength: '',
    referencing: '',
    discount:'',
  }

  const { handleChange, handleSubmit, handleBlur, handleReset, values, setValues, touched, errors } = useFormik({
    initialValues: initValue,
    validationSchema: validationSchema,
    onSubmit: values => {
      const value = values;
      console.log('submit Value',value);
      const requestData = new FormData();
      const tdata = Object.keys(value);
      tdata.forEach(val => {
        if(val === 'files'){
          const tempfile = [...value[val]]
          console.log('this is temp file',[...tempfile])
          return tempfile.forEach(v=>requestData.append(val, v));
        }
        requestData.append(val, value[val]);
      });
      sendRequest(requestData);
    }
  });


  console.log('tuched', touched);
  console.log('Errore', errors);
  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Add Post</h4>
              <p className={classes.cardCategoryWhite}>Complete profile</p>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Name"
                    id="name"
                    helperText={touched.name && errors.name}
                    error={Boolean(errors.name && touched.name)}
                    inputProps={{
                      name: 'name',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.name,
                      disabled: true
                    }}

                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Email"
                    id="email"
                    variant="outlined"
                    helperText={touched.email && errors.email}
                    error={Boolean(errors.email && touched.email)}
                    inputProps={{
                      name: 'email',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.email,
                      disabled: true
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Education"
                    id='education'
                    data={[
                      'secondary',
                      'senior secondary',
                      'graduation',
                      'college',
                      'university',
                      'doctorate',
                      'master'
                    ]}
                    helperText={touched.education && errors.education}
                    error={Boolean(errors.education && touched.education)}

                    inputProps={{
                      name: 'education',
                      label: 'Education',
                      value: values.education,
                      onChange: handleChange
                    }} />
                </GridItem>
            
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Paper Type"
                    id='paperType'
                    data={[
                      'Assignment',
                      'Bibliography',
                      'Business Proposal',
                      'Case Study',
                      'Course Work',
                      'Dissertation',
                      'Dissertation Proposal',
                      'Essay',
                      'Home Work',
                      'Report',
                      'Research Paper',
                      'Thesis',
                      'Thesis Proposal',
                      'Other'
                    ]}
                    helperText={touched.paperType && errors.paperType}
                    error={Boolean(errors.paperType && touched.paperType)}

                    inputProps={{
                      name: 'paperType',
                      label: 'Paper Type',
                      value: values.paperType,
                      onChange: handleChange
                    }} />
                </GridItem>
              
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Paper Length"
                    id='paperLength'
                    data={[
                      '1 Page/250 words',
                      '2 Pages/500 words',
                      '3 Pages/750 words',
                      '4 Pages/1000 words',
                      '5 pages/1250 words',
                      '6 Pages/1500 words',
                      '7 Pages/1750 words ',
                      '8 Pages/2000 words',
                      '9 Pages/2250 words',
                      '10 Pages/2500 words',
                      '11 Pages/2750 words',
                      '12 Pages/3000 words',
                      '13 Pages/3250 words',
                      '14 Pages/3500 words',
                      '15 Pages/3750 words',
                      '16 Pages/4000 words',
                      '17 Pages/4250 words',
                      '18 Pages/4500 words',
                      '19 Pages/4750 words',
                      '20 Pages/5000 words',
                      '21 Pages/5250 words',
                      '22 Pages/5500 words',
                      '23 Pages/5750 words',
                      '24 Pages/6000 words',
                      '25 Pages/6250 words',
                      '26 Pages/6500 words',
                      '27 Pages/6750 words',
                      '28 Pages/7000 words',
                      '29 Pages/7250 words',
                      '30 Pages/7500 words',
                      '31 Pages/7750 words',
                      '32 Pages/8000 words',
                      '33 Pages/8250 words',
                      '34 Pages/8500 words',
                      '35 Pages/8750 words',
                      '36 Pages/9000 words',
                      '37 Pages/9250 words',
                      '38 Pages/9500 words',
                      '39 Pages/9750 words',
                      '40 Pages/10000 words',
                    ]}
                    helperText={touched.paperType && errors.paperType}
                    error={Boolean(errors.paperType && touched.paperType)}

                    inputProps={{
                      name: 'paperLength',
                      label: 'Paper Length',
                      value: values.paperLength,
                      onChange: handleChange
                    }} />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Referencing Style"
                    id='referencing'
                    data={[
                      'Harvard',
                      'APA',
                      'MLA',
                      'Chicago',
                      'Footnotes',
                      'Footnotes and bibliography',
                      'AGLC',
                      'BMJ',
                      'MHRA',
                      'Oxford',
                      'OSCOLA',
                      'Vancouver',
                      'Turbian',
                      'Open'
                    ]}
                    helperText={touched.referencing && errors.referencing}
                    error={Boolean(errors.referencing && touched.referencing)}

                    inputProps={{
                      name: 'referencing',
                      label: 'Referencing Style',
                      value: values.referencing,
                      onChange: handleChange
                    }} />
                </GridItem>

                <GridItem xs={12} sm={12} md={6}>
                    <CountrySelect 
                     onChangeValue={value=>{
                       setValues({ ...values, country: `${value?.label} (${value?.code}) +${value?.phone}`
                       })}
                       } />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <DatePicker
                    helperText={errors.deadline}
                    error={Boolean(errors.deadline && touched.deadline)}
                    label='Deadline' onChange={(val) => setValues({
                      ...values,
                      deadline: val
                    })} value={values.deadline}
                  />
                </GridItem>
             
                <GridItem xs={12} sm={6}>
                  <CustomInput
                    labelText="Subject"
                    id="subject"
                    helperText={touched.subject && errors.subject}
                    error={Boolean(errors.subject && touched.subject)}
                    inputProps={{
                      name: "subject",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.subject
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Enter Contact"
                    id="contact"
                    helperText={touched.contact && errors.contact}
                    error={Boolean(errors.contact && touched.contact)}
                    inputProps={{
                      name: "contact",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.contact
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12}>
                  <CustomInput
                    labelText="Question"
                    id="question"
                    helperText={touched.question && errors.question}
                    error={Boolean(errors.question && touched.question)}
                    inputProps={{
                      name: "question",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.question
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>

              </GridContainer>
            

              <GridContainer>
                
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Discount Code if You have"
                    id="discount"
                    helperText={touched.discount && errors.discount}
                    error={Boolean(errors.discount && touched.discount)}
                    inputProps={{
                      name: "discount",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.discount
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                
              </GridContainer>
              <GridContainer>
                <GridItem>
                <OutlinedInput type='file'
                 inputProps={{ multiple: true }} onChange={
                  e=>{
                  const value = e.target.files
                  console.log(value);
                  setValues({
                    ...values,
                    files: value
                  })
                  }} />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button onClick={handleSubmit} color="primary">Add Order</Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
