import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import * as Yup from 'yup';

import InputLabel from "@material-ui/core/InputLabel";
import Box from '@mui/material/Box';
import { useFileUpload } from 'use-file-upload'
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import NormalSelect from 'components/NormalSelect'
import DatePicker from 'components/DatePicker'
import Avatar from '@mui/material/Avatar';


import avatar from "assets/img/faces/marc.jpg";
import useForm from "components/validation/useForm";
import Validator from "components/validation/Validator";
import { HOST } from '../../config.js'
import { ToastContext } from "index";
import { LoginContext } from "index.js";



const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function UserProfile() {
  const { setToastData } = React.useContext(ToastContext);
  const { userData } = React.useContext(LoginContext);


  const classes = useStyles();
  const [files, selectFiles] = useFileUpload();

  const sendRequest = (value) => {
    console.log(value)
    fetch(`${HOST}/api/order`, {
      method: 'POST',
      body: value
    }).then(res =>(res.json()))
      .then(res => {
        handleReset();
        if(!res.ok){
          setToastData({state: true, message: res.message, type: 'success'})
        }else{
          setToastData({state: true, message: res.message, type: 'error'})
        }
        console.log(res)
      })
  }

  React.useEffect(()=>{
    setValues({
        ...values,
        user: userData._id,
        name: userData.name,
        email: userData.email
    })
  },[userData])


  const validationSchema = Yup.object().shape({
    subject: Yup.string().required(),
    user: Yup.string().required(),
    name: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
    email: Yup.string().email('Invalid email').required('Email is Required'),
    question: Yup.string().required('Question is Required'),
    education: Yup.string().required('Education is Required'),
    file: Yup.string().notRequired()
  });

  const initValue = {
    user:'',
    subject:'',
    name:'',
    email:'',
    education:'',
    question:'',
    file:'',
  }

  const { handleChange, handleSubmit, handleBlur, handleReset, values, setValues, touched, errors } = useFormik({
    initialValues: initValue,
    validationSchema: validationSchema,
    onSubmit: values => {
      const value = values;
      console.log(value);
      const requestData = new FormData();
      const tdata = Object.keys(value);
      tdata.forEach(val => {
        if(val === 'file' && files){
          return requestData.append(val, files?.file)
        }
        requestData.append(val, value[val]);
      });
      sendRequest(requestData);
    }
  });

  console.log(values);

  console.log('tuched',touched);
  console.log('Errore',errors);
  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Add Post</h4>
              <p className={classes.cardCategoryWhite}>Complete profile</p>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Name"
                    id="name"
                    helperText={touched.name && errors.name}
                    error={Boolean(errors.name && touched.name)}
                    inputProps={{
                      name: 'name',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.name,
                      disabled: true
                    }}

                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Email"
                    id="email"
                    helperText={touched.email && errors.email}
                    error={Boolean(errors.email && touched.email)}
                    inputProps={{
                      name: 'email',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.email,
                      disabled: true
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                </GridContainer>
                <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <NormalSelect
                    labelText="Education"
                    id='education'
                    data={[
                    'secondary',
                    'senior secondary',
                    'graduation',
                    'college',
                    'university',
                    'doctorate',
                    'master'
                   ]}
                    helperText={touched.education && errors.education}
                    error={Boolean(errors.education && touched.education)}

                    inputProps={{
                      name: 'education',
                      label: 'Education',
                      value: values.education,
                      onChange: handleChange
                    }} />
                </GridItem>
                </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={6}>
                  <CustomInput
                    labelText="Subject"
                    id="subject"
                    helperText={touched.subject && errors.subject}
                    error={Boolean(errors.subject && touched.subject)}
                    inputProps={{
                      name: "subject",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.subject
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
              </GridContainer>
             
              <GridContainer>
              <GridItem xs={12} sm={6}>
                  <CustomInput
                    labelText="Question"
                    id="question"
                    helperText={touched.question && errors.question}
                    error={Boolean(errors.question && touched.question)}
                    inputProps={{
                      name: "question",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.question
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  
                  {/* <img src={files?.source || ''} alt="preview" /> */}
                  <Button color='primary' round onClick={() => {
                    selectFiles({ accept: "image/*" }, ({ name, size, source, file }) => {
                      console.log("Files Selected", { name, size, source, file });
                    })
                  }
                  } >
                    Image
                  </Button>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                <Avatar
                    alt="Remy Sharp"
                    src={files?.source || ''}
                    sx={{ width: '100%', height: '100%' }}
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button onClick={handleSubmit} color="primary">Add Order</Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
