import axios from "axios";
import io from "socket.io-client";
import { HOST } from "config.js";

const baseCustunUrl = HOST;

const tApi = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    Authorization: `bearer ${localStorage.getItem("studentToken")}`,
  },
});
const api = axios.create({
  baseURL: baseCustunUrl,
  headers: {
    "Content-Type": "application/json",
  },
});
const formApi = axios.create({
  baseURL: baseCustunUrl,
});
export const socket = io.connect(baseCustunUrl, {
  transports: ["websocket"],
  upgrade: false,
});
export const forgotApi = (d)=>api.post('/api/forgot/otp',d);
export const forgotVerifyApi = (d)=>api.post('/api/forgot/verify',d);
export const refreshApi = () => tApi.get('/api/refresh');
export const getUnseenMessages = i => api.get(`/api/message/unseen/user/${i}`);
export const setSeenOrderApi = (i) => api.get(`/api/message/unseen/order/${i}`);
export const getAdimnApi = ()=> api.get('/api/admin');
export const postOrderApi = (d) => formApi.post('/api/mainorder',d);
export const postTranslationOrderApi = (d) => formApi.post("/api/translation", d);
export const loginApi = (d) => api.post('/api/login',d);
export const getOrderMessageApi =(i)=>api.get(`/api/message/order/${i}`);
export const getUserTranslationOrderApi = (i, d) =>api.get(`/api/translation/user/${i}?page=${d.page}&row=${d.row}`);
export const getUserOrderApi = (i,d) => api.get(`/api/mainorder/user/${i}?page=${d.page}&row=${d.row}`)


export const adminLoginApi = (d) => api.post("/api/admin/login", d);
export const getAllUserApi = () => api.get("/api/users");
export const getAllOrderApi =(d,user)=>api.get(`/api/translation?page=${d.page}&row=${d.row}&user=${user}`);
export const getTranslationUesrsApi = (d, user) =>
  api.get(`/api/users?page=${d.page}&row=${d.row}&user=${user}`);
export const getChatOrderApi = () => api.get(`/api/translation/chat-order`);


