import React, { useState, useEffect, useContext, useCallback } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';

import MUIDataTable from "mui-datatables";
import TablePagination from '@mui/material/TablePagination';
import moment from 'moment';

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CustomDropDawn from 'components/CustomDropDawn';
import Button from "components/CustomButtons/Button.js";

import { CustumContex } from "client/layouts/Admin";
import { Route, useHistory, useLocation, Switch } from "react-router";
import { HOST } from '../../config.js'
import { LoginContext } from "client/index.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0",
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF",
    },
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
};

const useStyles = makeStyles(styles);

export default function Orders() {

  const history = useHistory();
  const [dataList, setDataList] = useState([]);
  const [refresh, setRefresh] = useState(false);
  const { setOrderId } = useContext(CustumContex);
  const { userData } = React.useContext(LoginContext);



  const [page, setPage] = useState(0)
  const [rowPerPage, setRowsPerPage] = useState(10);
  const [rowsTotal, setRowsTotal] = useState(10);


  useEffect(() => {
    fetch(`${HOST}/api/mainorder/user/${userData._id}`)
      .then(res => res.json())
      .then(res => {
        if (res.data) {
          (res.data).map(data => setDataList(doc => [...doc, [
            data._id,
            data._id,
            data.subject,
            data.education,
            moment(data.createdAt).format('MMM DD, YYYY'),
            moment(data.deadline,).format('MMM DD, YYYY'),
            data.paperLength,
            data.paperType,
            data.country,
            data.referencing,
            data.files,
          ]]));
        }
      });

    return () => {
      setDataList([]);
    }
  }, [userData]);


  const actionHander = (value, typ) => {
    if (typ === 'Message') {
      setOrderId(value);
      history.push('orderchats');
    } 
  }

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setRefresh(!refresh);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
    setRefresh(!refresh);
  };


  const columns = [
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const actionHanderTemp = (type) => {
            const typ = type.target.textContent;
            actionHander(value, typ);
          }

          return (
            <>
              <CustomDropDawn actionHander={actionHanderTemp} list={['Message']} />
            </>
          );
        }
      }
    },
    "Order Id", 'Subject', 'Education', 'Date', 'Deadline', 'Paper Length', 'Paper Type', 'Country', 'Referencing Style', 'Files'];


  const options = {
    filterType: 'checkbox',
    pagination: false,
    onRowsDelete: (value) => { deleteHander(value) },
    setRowProps: row => {
      const color = row[7] === "active" ? '#d9ffd5' : ' #ffa9a9 ';
      return {
        style: { background: color }
      };
    }
  };
  const classes = useStyles();
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Employees Table</h4>
            <p className={classes.cardCategoryWhite}>
              Here is Detail for All Employees
            </p>
          </CardHeader>
          <CardBody>
            <MUIDataTable
              title='Orders'
              data={dataList}
              columns={columns}
              options={options}
            />
            <TablePagination
              rowsPerPageOptions={[5, 10, 25, { label: '100', value: 100 }]}
              colSpan={3}
              count={+rowsTotal}
              rowsPerPage={rowPerPage}
              page={page}
              SelectProps={{
                inputProps: {
                  'aria-label': 'rows per page',
                },
                native: true,
              }}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
            />
          </CardBody>
        </Card>
      </GridItem>

    </GridContainer>
  );
}
