import React, { useState, useEffect } from 'react'
import { Row, Col, Form, Button, InputGroup, Nav, Tab, ListGroup } from 'react-bootstrap';
import { FaSearch, FaFolderPlus, FaPaperPlane } from 'react-icons/fa'
import styles from './ChatSupport.module.css'
import { IconContext } from 'react-icons';
import Chat from 'student/components/Chat/Chat'
import { useSelector, useDispatch } from 'react-redux';
import {
  getUserOrderApi,
  getUserTranslationOrderApi,
  getChatOrderApi,
} from "student/http";
import { setOrder } from 'student/store/order';
import { setSeenMessage } from 'student/store/unsm';
import { setReceiver } from 'student/store/order';
import { socket } from "student/http";



const ChatSupport = () => {
    const { user } = useSelector(state => state.auth);
    const { order } = useSelector(state => state.order);
    
    const dispatch = useDispatch();
    const [tableData, setTableData] = useState([]);
    const [isUnmount, setIsUnmount] = useState(false);
    const [searchData, setSearchData] = useState([]);

    const [totalData, setTotalData] = useState(5);
    const [pageData, setPageData] = useState({
        page: 0, row: 5
    })


    React.useEffect(async () => {
        if (user._id) {
            const api = user.role === 'admin' ? getChatOrderApi() : getUserTranslationOrderApi(
                user._id,
                pageData
              )
            try {
              const { data } = await api;
            //   console.log(data);
              if (data?.data) {
                if (!isUnmount) {
                  setTableData(data.data);
                  setTotalData(data.total);
                }
              }
            } catch (e) {
                console.log(e);
            }
        }
        return () => {
            setTableData([]);
            setIsUnmount(true);
        };

    }, [user, pageData]);

    const navClickHandler = (v) =>{
        dispatch(setOrder(v));
        dispatch(setSeenMessage(v._id));
        if(user.role !== 'user'){
             dispatch(setReceiver({ _id: v.userId }));
            //  console.log('user', v.userId)
             socket.emit("joinChat", { sender: user, _id: v.userId });
        }
    }

    return (
        <div className={styles.bgLightWhite}>
            <Tab.Container id="left-tabs-example" defaultActiveKey="first">
                <Row>
                    <Col md={5}>
                        <div className={`${styles.darkBlue} rounded p-2 h-100`}>
                            <Row>
                                <Col md={12}>
                                    <h4 className={styles.chat}>Chat</h4>
                                </Col>

                                <Form.Group as={Col} md="12" controlId="validationCustomUsername">
                                    <InputGroup hasValidation>
                                        <Form.Control
                                            type="text"
                                            placeholder="Search"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <Button variant="secondary" >
                                            <FaSearch />
                                        </Button>
                                    </InputGroup>
                                    {searchData.length ? <ListGroup className='p-1'>
                                        { searchData.map(e => (
                                            <ListGroup.Item key={e._id}>{e._id}</ListGroup.Item>
                                        ))}
                                    </ListGroup>: ''}
                                </Form.Group>
                            </Row>
                            <Nav variant="pills" className="flex-column pt-2">
                                {tableData.map(e => (
                                    <Nav.Item key={e._id}>
                                        <Nav.Link onClick={()=>navClickHandler(e)} className={`${styles.navList} ${e._id === order._id ? styles.navListActive : ''} `} eventKey={`first`}>Order Id: {`#${e._id}`}</Nav.Link>
                                    </Nav.Item>
                                ))}
                            </Nav>
                        </div>
                    </Col>
                    <Col md={7}>
                        <Tab.Content>
                            <Tab.Pane eventKey="first">
                                <Chat />
                            </Tab.Pane>
                        </Tab.Content>
                    </Col>
                </Row>
            </Tab.Container>
        </div>
    )
}

export default ChatSupport
