import { Container, Row, Col, Tab } from "react-bootstrap";
import React, { useEffect, useState } from "react";
import styles from "student/App.module.css";
import { Switch, Route, Redirect, withRouter } from "react-router-dom";
import Sidebar from "student/components/Sidebar/Sidebar";
import NavbarBoot from "student/components/Navbar/Navbar";
import { useDispatch, useSelector } from "react-redux";
import { refreshApi } from "student/http";
import { setLogIn } from "student/store/auth";
import { socket } from "student/http";
import { getAdimnApi } from "student/http";
import { setReceiver } from "student/store/order";
import { getUnseenMessages } from "student/http";
import { setUnSeenMessages } from "student/store/unsm";
import { addUnSeenMessage } from "student/store/unsm";
import DLayout from 'student/layout/DLayout';

const Layout = ({ routers = [], match }) => {
  const { user } = useSelector((s) => s.auth);
  const dispatch = useDispatch();
  const [isUnmount, setIsUnmount] = useState(false);

  useEffect(() => {
    socket.on("userMessage", (data) => {
      dispatch(addUnSeenMessage(data));
    });
  }, [socket]);

  // console.log(user)

  useEffect(() => {
    if (user.role === 'user') {
      socket.emit("joinChat", { sender: user, _id: user._id });
      (async () => {
        try {
          const { data } = await getUnseenMessages(user._id);
          if (data) {
            if (!isUnmount) {
              dispatch(setUnSeenMessages(data.data));
            }
          }
        } catch (err) {
          console.log(err);
        }
      })();

      (async () => {
        try {
            const { data } = await getAdimnApi();
            if (data) {
              if (!isUnmount) {
                dispatch(setReceiver(data.data));
              }
            }
        } catch (e) {
          console.log(e);
        }
      })();
    }

    return () => {
      setIsUnmount(true);
    };
  }, [user]);
  useEffect(() => {
    
    (async () => {
      if (user._id) return;
      try {
        const { data } = await refreshApi();
        if (data) {
          if (!isUnmount) {
            dispatch(setLogIn(data.data));
          }
        }
      } catch (err) {
        console.log(err);
      }
    })();
    return () => {
      setIsUnmount(true);
    };
  }, []);

  return (
    <div className={styles.main}>
      <NavbarBoot />
      <section>
        <Container className="mt-4 p-2 min-vh-100">
          <Row className="position-relative">
            <Col md={3}>
              <Sidebar routers={routers} />
            </Col>
            <Col md={9} className="pr-5 position-relative pb-5 pb-md-0">
              <Switch>
                {routers.map((e, i) => {
                  return (
                    <Route
                      key={i}
                      path={`${match.path}${e.path}`}
                      render={() => {
                        return (
                          <Switch>
                            {e.children
                              ? e.children.map((c, j) => (
                                  <Route
                                    key={j}
                                    path={`${match.path}${e.path}${c.path}`}
                                    children={<DLayout title={c.name}>{c.component}</DLayout>}
                                  />
                                ))
                              : ""}
                            <Route
                              path={`${match.path}${e.path}`}
                              component={e.component}
                            />
                          </Switch>
                        );
                      }}
                    />
                  );
                    
                })}
                <Redirect to={`${match.path}/dashbord`} />
              </Switch>
            </Col>
          </Row>
        </Container>
      </section>
    </div>
  );
};

export default withRouter(Layout);
